import _tool from '@/static/zaiui/util/tools.js';
export default {
	name: 'grid-sort-list',
	props: {
		list_data: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	methods: {
		moreTap() {
			let index = this.list_data.length;
			this.listTap({type: 'more'},index);
		},
		listTap(data,index) {
			this.$emit('listTap', {
				data,
				index
			});
		}
	}
}
